.App {
  text-align: center;
  padding: 5rem;
 
  background-color: rgb(0, 0, 0);
  font-size: 82.5%;

  @media only screen and (max-width: 500px) {
    padding: 1rem;
    font-size: 72%;
    padding-top: 5rem;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
  
}

.recipe-box {
  padding: 2.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  gap: 2rem;
  color: white;
  margin-top: 3rem;

  @media only screen and (max-width: 500px) {
    padding: .7rem;
  }
}

.Recipe {
  background-color: rgb(29, 29, 29);
  padding: 1rem;
  border-radius: 5px;

  h2 {
    font-size: 1.5rem;
    margin-bottom: .5rem;
  }

  p {
    color: rgb(212, 212, 212);
  }

  &__imgbox {
    height: 15rem;
    overflow: hidden;
    border-radius: 5px;

    @media only screen and (max-width: 500px) {
      height: 10rem;
    }

    img {
      width: 100%;
    }
  }

  li {
    text-align: left !important;
    margin: .5rem;
  }
}

.search-bar {
  padding: .3rem;

  &:focus {
    outline: none;
  }
}

.search-button {
  margin: 1rem;
  background-color: green;
  color: white;
  padding: .3rem;
  border-radius: 2px;
  border: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}